import React, { createContext, useState, useContext, useEffect } from 'react';
import api from '../services/api';

const ReclamationContext = createContext();

export const ReclamationProvider = ({ children }) => {
  const [reclamations, setReclamations] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchReclamations = async () => {
    setLoading(true);
    try {
      const response = await api.get('/reclamations');
      setReclamations(response.data);
      setError(null);
    } catch (err) {
      setError(err.response?.data?.message || 'Erreur de chargement');
      console.error('Fetch error:', err);
    } finally {
      setLoading(false);
    }
  };

  const createReclamation = async (reclamationData) => {
    try {
      const response = await api.post('/reclamations', reclamationData);
      setReclamations(prev => [...prev, response.data]);
      return response.data;
    } catch (err) {
      throw err.response?.data || err;
    }
  };

  const updateReclamation = async (id, reclamationData) => {
    try {
      const response = await api.put(`/reclamations/${id}`, reclamationData);
      setReclamations(prev => prev.map(r => r._id === id ? response.data : r));
      return response.data;
    } catch (err) {
      throw err.response?.data || err;
    }
  };

  const deleteReclamation = async (id) => {
    try {
      await api.delete(`/reclamations/${id}`);
      setReclamations(prev => prev.filter(r => r._id !== id));
    } catch (err) {
      throw err.response?.data || err;
    }
  };

  useEffect(() => {
    fetchReclamations();
  }, []);

  return (
    <ReclamationContext.Provider
      value={{
        reclamations,
        loading,
        error,
        fetchReclamations,
        createReclamation,
        updateReclamation,
        deleteReclamation
      }}
    >
      {children}
    </ReclamationContext.Provider>
  );
};

export const useReclamation = () => {
  const context = useContext(ReclamationContext);
  if (!context) {
    throw new Error('useReclamation must be used within a ReclamationProvider');
  }
  return context;
};