import React from 'react';
import { Card, Typography, ButtonGroup, Button, Chip } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import ReclamationStatusBadge from './ReclamationStatusBadge';
import { formatDate } from '../utils/helpers';

const ReclamationItem = ({ reclamation }) => {
  const navigate = useNavigate();
  const isOverdue = new Date(reclamation.dateEcheance) < new Date() && reclamation.statut !== 'resolu';

  return (
    <Card className="reclamation-item">
      <div className="item-header">
        <Typography variant="h6">{reclamation.titre}</Typography>
        <div className="status">
          <ReclamationStatusBadge status={reclamation.statut} />
          {isOverdue && <Chip label="En retard" color="error" size="small" />}
        </div>
      </div>

      <Typography variant="body2" color="textSecondary">
        Catégorie: {reclamation.categorie}
      </Typography>

      <Typography variant="body2">
        Échéance: {formatDate(reclamation.dateEcheance)}
      </Typography>

      <ButtonGroup className="actions">
        <Button 
          size="small" 
          onClick={() => navigate(`/reclamations/${reclamation._id}`)}
        >
          Détails
        </Button>
        <Button 
          size="small" 
          onClick={() => navigate(`/reclamations/${reclamation._id}/edit`)}
        >
          Modifier
        </Button>
      </ButtonGroup>
    </Card>
  );
};

export default ReclamationItem;