import React from 'react';
import { Chip } from '@mui/material';

const statusColors = {
  nouveau: 'primary',
  en_cours: 'warning',
  resolu: 'success',
  rejete: 'error'
};

const statusLabels = {
  nouveau: 'Nouveau',
  en_cours: 'En cours',
  resolu: 'Résolu',
  rejete: 'Rejeté'
};

const ReclamationStatusBadge = ({ status }) => {
  return (
    <Chip 
      label={statusLabels[status] || status} 
      color={statusColors[status] || 'default'} 
      size="small"
    />
  );
};

export default ReclamationStatusBadge;