import React from 'react';
import { useReclamation } from '../context/ReclamationContext';
import ReclamationItem from './ReclamationItem';
import Spinner from './Spinner';
import Alert from './Alert';
import { Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const ReclamationList = () => {
  const { reclamations, loading, error } = useReclamation();
  const navigate = useNavigate();

  if (loading) return <Spinner />;
  if (error) return <Alert severity="error" message={error} />;

  return (
    <div className="reclamation-list">
      <div className="header">
        <h2>Liste des Réclamations</h2>
        <Button 
          variant="contained" 
          onClick={() => navigate('/reclamations/new')}
        >
          Nouvelle Réclamation
        </Button>
      </div>

      <div className="list">
        {reclamations.length === 0 ? (
          <Alert severity="info" message="Aucune réclamation trouvée" />
        ) : (
          reclamations.map(reclamation => (
            <ReclamationItem key={reclamation._id} reclamation={reclamation} />
          ))
        )}
      </div>
    </div>
  );
};

export default ReclamationList;