import React, { useState } from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { 
  Button, 
  TextField, 
  Select, 
  MenuItem, 
  FormControl, 
  InputLabel,
  CircularProgress,
  Typography,
  Paper,
  Box
} from '@mui/material';
import { useReclamation } from '../context/ReclamationContext';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import api from '../services/api';
import ReclamationStatusBadge from './ReclamationStatusBadge';

const ReclamationForm = ({ initialValues, isEdit = false }) => {
  const { createReclamation, updateReclamation } = useReclamation();
  const navigate = useNavigate();
  const [predictions, setPredictions] = useState(null);
  const [isPredicting, setIsPredicting] = useState(false);
  const [predictionError, setPredictionError] = useState(null);

  const validationSchema = Yup.object().shape({
    titre: Yup.string()
      .required('Le titre est obligatoire')
      .max(100, 'Le titre ne peut pas dépasser 100 caractères'),
    description: Yup.string()
      .required('La description est obligatoire')
      .max(1000, 'La description ne peut pas dépasser 1000 caractères'),
    categorie: Yup.string()
      .required('La catégorie est obligatoire')
      .oneOf(['technique', 'service', 'facturation', 'autre'], 'Catégorie invalide'),
    statut: isEdit ? Yup.string()
      .oneOf(['nouveau', 'en_cours', 'resolu', 'rejete'], 'Statut invalide') : Yup.string()
  });

  const handlePredict = async (values) => {
    setIsPredicting(true);
    setPredictionError(null);
    try {
      const response = await api.post('/sla/predict', {
        category: values.categorie,
        description: values.description
      });
      setPredictions(response);
    } catch (err) {
      setPredictionError('Erreur lors de la prédiction. Veuillez réessayer.');
      console.error('Prediction error:', err);
    } finally {
      setIsPredicting(false);
    }
  };

  const handleSubmit = async (values, { setSubmitting }) => {
    try {
      const submissionData = {
        ...values,
        ...(predictions && {
          priorite: predictions.priority,
          dateEcheance: new Date(Date.now() + predictions.estimated_resolution_days * 24 * 60 * 60 * 1000)
        })
      };
      
      if (isEdit) {
        await updateReclamation(initialValues._id, submissionData);
        toast.success('Réclamation mise à jour avec succès!');
      } else {
        await createReclamation(submissionData);
        toast.success('Réclamation créée avec succès!');
      }
      navigate('/reclamations');
    } catch (err) {
      toast.error(err.message || 'Erreur lors de la soumission');
      console.error('Submission error:', err);
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ values, isSubmitting, isValid, dirty }) => (
        <Form>
          <Field name="titre">
            {({ field, meta }) => (
              <TextField
                {...field}
                label="Titre"
                fullWidth
                margin="normal"
                error={meta.touched && !!meta.error}
                helperText={meta.touched && meta.error}
              />
            )}
          </Field>

          <Field name="description">
            {({ field, meta }) => (
              <TextField
                {...field}
                label="Description"
                multiline
                rows={4}
                fullWidth
                margin="normal"
                error={meta.touched && !!meta.error}
                helperText={meta.touched && meta.error}
              />
            )}
          </Field>

          <Field name="categorie">
            {({ field, meta }) => (
              <FormControl fullWidth margin="normal" error={meta.touched && !!meta.error}>
                <InputLabel>Catégorie</InputLabel>
                <Select {...field} label="Catégorie">
                  <MenuItem value="technique">Technique</MenuItem>
                  <MenuItem value="service">Service</MenuItem>
                  <MenuItem value="facturation">Facturation</MenuItem>
                  <MenuItem value="autre">Autre</MenuItem>
                </Select>
                <ErrorMessage name="categorie" component="div" className="error-message" />
              </FormControl>
            )}
          </Field>

          {isEdit && (
            <Field name="statut">
              {({ field, meta }) => (
                <FormControl fullWidth margin="normal" error={meta.touched && !!meta.error}>
                  <InputLabel>Statut</InputLabel>
                  <Select {...field} label="Statut">
                    <MenuItem value="nouveau">Nouveau</MenuItem>
                    <MenuItem value="en_cours">En cours</MenuItem>
                    <MenuItem value="resolu">Résolu</MenuItem>
                    <MenuItem value="rejete">Rejeté</MenuItem>
                  </Select>
                  <ErrorMessage name="statut" component="div" className="error-message" />
                </FormControl>
              )}
            </Field>
          )}

          {!isEdit && (
            <Box mt={2} mb={2}>
              <Button
                type="button"
                variant="outlined"
                color="secondary"
                onClick={() => handlePredict(values)}
                disabled={isPredicting || !isValid || !dirty}
                startIcon={isPredicting ? <CircularProgress size={20} /> : null}
              >
                {isPredicting ? 'Prédiction en cours...' : 'Prédire la priorité et délai'}
              </Button>
              
              {predictionError && (
                <Typography color="error" variant="body2" mt={1}>
                  {predictionError}
                </Typography>
              )}
            </Box>
          )}

          {predictions && !isEdit && (
            <Paper elevation={2} sx={{ p: 2, mb: 2, bgcolor: 'background.paper' }}>
              <Typography variant="subtitle1" gutterBottom>
                Prédictions du modèle
              </Typography>
              <Typography>
                <strong>Priorité estimée:</strong> {predictions.priority}
              </Typography>
              <Typography>
                <strong>Temps de résolution estimé:</strong> {predictions.estimated_resolution_days} jours
              </Typography>
            </Paper>
          )}

          <Button
            type="submit"
            variant="contained"
            color="primary"
            disabled={isSubmitting || (!isEdit && !predictions)}
            sx={{ mt: 2 }}
            startIcon={isSubmitting ? <CircularProgress size={20} /> : null}
          >
            {isSubmitting ? 'Envoi en cours...' : isEdit ? 'Mettre à jour' : 'Créer la réclamation'}
          </Button>
        </Form>
      )}
    </Formik>
  );
};

ReclamationForm.defaultProps = {
  initialValues: {
    titre: '',
    description: '',
    categorie: 'technique',
    statut: 'nouveau'
  }
};

export default ReclamationForm;